___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  font-weight: 600;
}

body {
  color: white;
  background-color: #000000;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

#attr-div {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

a {
  font-size: 0.5vw;
  color: white;
  position: absolute;
  bottom: 0;
}

.search-div {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

#search-input {
  width: 12vw;
  height: 5vh;
  margin: 25px;
  background-color: #181717;
  color: white;
  border: 1px solid white;
  border-radius: 5px;
  padding-left: 5px;
}

.main-content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  width: 90vw;
  height: 70vh;
  gap: 120px;
}

#city {
  font-weight: 800;

  font-size: clamp(1rem, 10vw, 5rem);
  text-transform: capitalize;
}

#weather-type {
  font-size: clamp(2rem, 2.5vw, 3rem);
  font-weight: 500;
}

#giphy-sticker {
  width: 15vw;
  height: 20vh;
}

#temperature {
  font-size: 100px;
  font-size: clamp(1.2rem, 11vw, 6rem);
}

sup {
  font-size: 35px;
}

.small-weather-card {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

#more-weather-info {
  display: flex;
  flex-direction: column;

  gap: 5px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #2196f3;

  transition: 0.4s;
  border-radius: 4px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

/* Slider moves when checked */
input:checked + .slider:before {
  transform: translateX(26px);
}

.label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 12px;
  font-weight: bold;
  color: #fff;
  user-select: none;

  transition: opacity 0.4s;
}

/* Label "C" (off state) */
.label.off {
  left: 8px;
  opacity: 1;
}

/* Label "F" (on state) */
.label.on {
  right: 8px;
  opacity: 1;
}
